.todoFormContainer {
	margin-bottom: 30px;
}

.todoFormContainer form {
    display: grid;
    justify-items: center;
    gap: 5px;
}

.todoFormContainer input {
    width: 40%;
    height: 30px;
    font-size: 1.1rem;
}

.todoFormContainer button {
	margin-top: 10px;
    height: 35px;
    cursor: pointer;
    background-color: coral;
    font-size: 1.1em;
}

.todoFormContainer button:hover {
    background-color: darkred;
}

.todoFormContainer input, 
.todoFormContainer button {
	border: none;
	border-radius: 4px;
	padding: 5px 5px;
}
