.todo {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    margin: 15px 0;
    font-size: 1.1rem;
    border-radius: 4px;
    border: 2px solid rgb(108, 199, 69);
    color: rgb(2, 2, 2);
    background-color: #ccc599;
    width: 100%;
}

.todoText {
    width: 100%;
    text-align: left;
}

.todoIcon {
    font-size: 1.1rem;
    margin-right: 12px;
    color: rgb(12, 68, 68);
}
